var Web3 = require("web3");

const stakeNft = async (tokenId, address) => {
    if (window.ethereum) {
        window.web3 = new Web3(window.ethereum);
        window.ethereum.enable();
    } else if (window.web3) {
        window.web3 = new Web3(window.web3.currentProvider);
    } else {
        window.alert(
            "Non-Ethereum browser detected. You should consider trying MetaMask!"
        );
    }
    var web3 = window.web3;
    
    const PremierStaking = require("./PremierStaking.json");
    const contractAddress = web3.utils.toChecksumAddress(process.env.REACT_APP_PREMIER_STAKING_ADDRESS);
    const contract = new web3.eth.Contract(PremierStaking.abi, contractAddress);
    
    let e;

    try {
        e = await contract.methods.stakeOne(tokenId).estimateGas({
            from: address
        })
    } catch (u) {
        
        return { success: false, type: 'estimategas' }
    }

    let d = await web3.eth.getGasPrice();

    let c;
    
    try {
        c = await contract.methods.stakeOne(tokenId).send({
            from: address,
            gas: parseInt(e),
            gasPrice: parseInt(1.2 * d),
            maxFeePerGas: null,
        })
    } catch (u) {
        return {success: false, type:"stake"};
    }

    if(c){
        return {success: c.status, type:"stake"};
    }
}

export default stakeNft;