import React from 'react';
import { useEffect, useState } from "react";
import AccountSectionGrid from 'components/AccountSectionGrid/AccountSectionGrid';
import { PostPlaceholder } from 'components/UI/ContentLoader/ContentLoader';
import AxiosFetch from 'library/hooks/AxiosFetch';

const AgentItemLists = ({ account }) => {
  const [tokens, setTokens] = useState([]);

  const fetchData = async () => {
    const url = '/getTokensByOwner';
    const body = {
      address: account,
    }
    const res = await AxiosFetch(url, body, "POST");
    if (res.data.success) {
      setTokens(res.data.data);
    }
  }

  useEffect(fetchData, [account]);  

  return (
    <div>
      {account === null &&
        <h3> You need to connect your wallet. </h3>
      }
      {account &&
        <AccountSectionGrid
          data={tokens}
          totalItem={tokens.length}
          columnWidth={[1 / 2, 1 / 2, 1 / 3, 1 / 4, 1 / 5, 1 / 6]}
          placeholder={<PostPlaceholder />}
          account={account}
          updateTokenStaking = {fetchData}
        />
      }
    </div>
  );
};

export default AgentItemLists;
